.projects {
  background-color: #0b0b0b;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border: 1px solid;
  padding: 150px 0;
  @media screen and (min-height: 800px) and (min-width: 768px) and (max-width: 1200px) {
    height: fit-content
  }
  @media screen and (max-width: 600px) {
    height: fit-content
  }
  @media screen and  (max-height: 500px){
    height: fit-content;
    padding-top: 5%;
    padding-bottom: 5%;
  }

  .projectsCont {
    width: 80%;
    border: 1px solid;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    @media screen and (min-height: 800px) and (min-width: 768px) and (max-width: 1200px) {
      margin-bottom: 16%;
      margin-top: 16%;
    }
    @media screen and (max-width: 600px) {
      margin-bottom: 27%;
      margin-top: 27%;
    }
    @media screen and  (max-height: 500px){
      justify-content: flex-start;
    }


    .projectsCompCont {
      width: 100%;
      flex-wrap: wrap;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      border: 1px solid;
      gap: 20px;
      @media screen and (min-height: 800px) and (min-width: 768px) and (max-width: 1200px) {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
      }
      @media screen and (max-width: 600px) {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
      }
      @media screen and  (max-height: 500px){
        align-items: flex-start;
      }
    }
  }
}






